<template>
  <div class="login">
    <!-- 导航 -->
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <!-- 内容 -->
    <div class="content">
      <div class="content-bg">
        <div class="content-box">
          <div class="content-list">
            <div class="tabbar">
              <div
                class="tabbar-item"
                :class="[index === dlIndex ? 'active' : '']"
                v-for="(item, index) in dl"
                :key="index"
                @click="changeType(index)"
              >{{ item }}</div>
            </div>
            <div class="account">
              <el-form ref="loginForm" :model="form">
                <el-form-item prop="pass">
                  <el-row class="text">{{ $t('login.account') }}</el-row>
                  <el-row class="el-input-border" style="display: flex;">
                    <div v-show="dlIndex === 0" class="el-input-choose" @click="showPhone">
                      <div class="el-value">{{ form.prefix }}</div>
                      <i class="el-icon el-icon-caret-bottom"></i>
                    </div>
                    <el-input
                      class="el-input-row"
                      v-model="form.account"
                      type="text"
                      auto-complete="off"
                    ></el-input>
                    <div class="el-list-box" v-show="countryTelShow">
                      <div class="el-list-box-item" v-for="(item, index) in phoneList" :key="index" @click="choosePhone(index)">
                        <span class="country">{{ item.country }}</span>
                        <span class="pre">{{ item.prefix }}</span>
                      </div>
                    </div>
                  </el-row>
                </el-form-item>
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('login.password') }}</el-row>
                  <el-input
                    class="el-input-row"
                    v-model="form.password"
                    type="password"
                    auto-complete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="login">{{ $t('login.login') }}</el-button>
                  <span class="forgetpassword" @click="path('/forgetpwd')">{{ $t('login.forgetpwd') }}</span>
                </el-form-item>
              </el-form>
              <div class="bz">
                <p v-for="(item, index) in $t('login.bz')" :key="index">{{ item }}</p>
                <p class="forgetpassword" @click="path('/register')">{{ $t('login.register') }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getTelCode } from '@/api/index'

export default {
  components: {
    AoNav
  },
  data() {
    return {
      dlIndex: 0,
      dl: this.$t('login.dl'),
      form: {
        prefix: "",
        account: "",
        password: ""
      },
      countryTelShow: false,
      phoneList: []
    };
  },
  mounted() {
    var query = this.$route.query
    if (query.account) {
      this.dlIndex = parseInt(query.index)
      this.form.prefix = query.prefix
      this.form.account = query.account
      this.form.password = query.password
    }
    getTelCode().then(res => {
      this.form.prefix = res[0].prefix
      this.phoneList = res
    })
  },
  methods: {
    login() {
      if (!this.form.account || !this.form.password) {
        this.$message({ message: this.$t('login.message'), type: 'error' })
      }
      this.loading = true
      this.form.index = this.dlIndex
      this.$store.dispatch('login', this.form).then(() => {
        this.$router.push({ path: '/index' })
      }).catch(() => {}).finally(() => {
        this.loading = false
      })
    },
    path(path) {
      this.$router.push({ path })
    },
    showPhone() {
      this.countryTelShow = !this.countryTelShow
    },
    choosePhone(index) {
      this.form.prefix = this.phoneList[index].prefix
      this.showPhone()
    },
    changeType(index) {
      this.dlIndex = index;
      if (this.countryTelShow) this.countryTelShow = false
    }
  }
};
</script>

<style lang="scss">
.login {
  .content {
    background-color: #1a243b;
  }
  .content-bg {
    background: url(/static/image/bg_login.png) center bottom 316px repeat-x,
      -webkit-linear-gradient(top, #21263f, #171c2b);
  }
  .content-box {
    width: 1200px;
    // margin: 0 auto;
    padding-top: 93px;
    overflow: hidden;
    min-height: 880px;
  }
  .content-list {
    position: relative;
    padding: 0 0 60px 30px;
    .tabbar {
      display: flex;
      .tabbar-item {
        padding-bottom: 5px;
        margin-right: 40px;
        cursor: pointer;
        color: #c7cce6;
        &.active {
          color: #7a98f7;
          border-bottom: 2px solid #7a98f7;
        }
      }
    }
    .account {
      display: flex;
    }
    .account .el-form {
      width: 520px;
      .text {
        display: block;
        height: 22px;
        color: #61688a;
        font-size: 12px;
      }
      .el-input-border {
        border: 1px solid #4e5b85;
        border-radius: 3px;
        position: relative;
        margin-top: 8px;
        .el-input-row {
          margin-top: 0;
          border: none;
        }
        .el-list-box {
          width: 100%;
          position: absolute;
          z-index: 5;
          left: 0;
          right: 0;
          top: 52px;
          height: 250px;
          overflow-y: auto;
          box-shadow: 0 2px 6px 0 #000;
          border-color: #d1d3df;
          background-color: #171c2b;
          color: #9194a4;
        }
        .el-list-box-item {
          padding: 15px;
          border-bottom: 1px solid #303b4b;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
      }
      .el-input-choose {
        display: flex;
        width: 146px;
        align-items: center;
        background-color: #1e2235;
        border-right: 1px solid #4e5b85;
        cursor: pointer;
        .el-value {
          flex: 1;
          text-indent: 0.5rem;
        }
        .el-value,
        .el-icon {
          width: 2.6rem;
          color: #c7cce6;
          font-size: 14px;
        }
      }
      .el-input-row {
        width: 520px;
        min-height: 46px;
        border: 1px solid #4e5b85;
        color: #c7cce6;
        font-size: 14px;
        border-radius: 3px;
        background-color: #1e2235;
        position: relative;
        margin-top: 8px;
      }
      .el-input__inner {
        border: none;
        height: 44px;
        padding-left: 10px;
        padding-right: 10px;
        background: transparent;
        color: #c7cce6;
      }
      .el-button {
        flex: 1;
        width: 200px;
        display: inline-block;
        line-height: 46px;
        background-color: #7a98f7;
        border-radius: 4px;
        color: #fff;
        border: none;
        padding: 0;
      }
      .forgetpassword {
        font-size: 14px;
        color: #7a98f7;
        cursor: pointer;
        margin-left: 1rem;
      }
    }
    .account .bz {
      margin-left: 5rem;
      margin-top: 2rem;
      font-size: 14px;
      p {
        font-size: 14px;
        color: #61688a;
        line-height: 24px;
        &.forgetpassword {
          font-size: 14px;
          color: #7a98f7;
          cursor: pointer;
          margin-top: 1rem;
        }
      }
    }
  }
}
</style>



